.menu-item {
  min-width: 30%;
  height: 400px;
  flex: 0 32%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  background-position: center;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  overflow: hidden;
  margin: 4px;

  &:first-child {
    flex-basis: 100%;
    height: 760px;
  }

